<template>
  <canvas ref="container" style="width: 100%;height: 100%" :style="{}"></canvas>
</template>

<script>
import * as THREE from "three";
export default {
  name: "threeTexture",
  props: {
    h: Number
  },
  data() {
    return {
      render: null,
      camera: null,
      scene: null,
      player: true,
      cube: null,
      cameraOpt: {
        angle: 70,
        min: 0.1,
        max: 1000
      }
    };
  },
  mounted() {
    console.log(1);
    this.renderAll();
  },
  methods: {
    renderAll() {
      this.renderInit();
      this.renderCube();
      this.animation();
    },
    renderInit() {
      const w = this.$refs.container.clientWidth;
      const h = this.$refs.container.clientHeight;
      this.render = new THREE.WebGLRenderer({
        canvas: this.$refs.container
      });
      this.render.setSize(w, h);
      this.camera = new THREE.PerspectiveCamera(
        this.cameraOpt.angle,
        w / h,
        this.cameraOpt.min,
        this.cameraOpt.max
      );
      this.camera.position.set(0, -80, 80);
      this.camera.lookAt(0, 0, 0);
      this.scene = new THREE.Scene();
    },
    tex() {
      function randomC(num, ctx) {
        const colors = ctx.createLinearGradient(
          0,
          0,
          0,
          ctx.canvas.height
        );
        for (let i = 0; i <= num; i++) {
          colors.addColorStop(
            i / num,
            `hsl(${Math.round(Math.random() * 255)}, ${Math.round(Math.random() * 100)}%, 50%)`
          );
        }
        return colors;
      }
      const canvas = document.createElement("canvas");
      const ctx = canvas.getContext("2d");
      ctx.fillStyle = randomC(10, ctx);
      ctx.fillRect(0, 0, ctx.canvas.width, ctx.canvas.height);
      return canvas;
    },
    renderCube() {
      const cubeGeometry = new THREE.BoxBufferGeometry(20, 20, 20);
      const texture = new THREE.CanvasTexture(this.tex());
      texture.needsUpdate = true;
      texture.wrapS = THREE.ClampToEdgeWrapping;
      texture.wrapT = THREE.ClampToEdgeWrapping;
      const material = new THREE.MeshBasicMaterial({
        map: texture,
        transparent: true
      });
      this.cube = new THREE.Mesh(cubeGeometry, material);
      this.scene.add(this.cube);
    },
    animation() {
      if (this.cube) {
        this.cube.rotation.x += 0.01;
        this.cube.rotation.y += 0.01;
      }
      this.render.render(this.scene, this.camera);
      requestAnimationFrame(() => {
        if (this.player) {
          this.animation();
        }
      });
    }
  }
};
</script>

